import { NextResponse } from 'next/server'
import { createClient } from '@/lib/supabase/server'
import { prisma } from '@/lib/prisma'

export async function GET() {
  try {
    const events = await prisma.gameEvent.findMany({
      take: 20,
      orderBy: { createdAt: 'desc' }
    })

    // Get usernames for events
    const supabase = await createClient()
    
    const eventsWithUsernames = await Promise.all(
      events.map(async (event) => {
        if (event.userId) {
          try {
            const { data: { user } } = await supabase.auth.admin.getUserById(event.userId)
            return {
              ...event,
              username: user?.user_metadata?.username || user?.email?.split('@')[0] || 'Unknown'
            }
          } catch {
            return { ...event, username: 'Unknown' }
          }
        }
        return event
      })
    )

    return NextResponse.json({ events: eventsWithUsernames })
  } catch (error) {
    console.error('GET /api/events error:', error)
    return NextResponse.json({ error: 'Failed to fetch events' }, { status: 500 })
  }
}